<template>
  <div class="dialog-container">
    <div class="dialog-bg"></div>
    <div class="dialog-box" :class="{'email-dialog-box': isPc,'email-dialog-box-mobile': !isPc}">
      <div class="first-row">
        <span>重置密码</span>
        <i @click="closeDialog()"></i>
      </div>
      <span class="tips-one">方便找回服务，接收最新消息。</span>
      <a-form-model :model="form" :label-col="{span: 5}" :wrapper-col="{span: 14}">
        <a-form-model-item label="旧密码" required>
          <a-input type="password" v-model="form.password" placeholder="请输入旧密码"/>
        </a-form-model-item>
        <a-form-model-item label="新密码" required>
          <a-input type="password" v-model="form.new_password" placeholder="请输入新密码"/>
        </a-form-model-item>
        <a-form-model-item label="重复新密码" required>
          <a-input type="password" v-model="form.new_password2" placeholder="请再输入新密码"/>
        </a-form-model-item>
      </a-form-model>
      <div class="error-message">
        <span style="position: absolute;color: red;">{{ errorMessage }}</span>
      </div>
      <div class="btn-row">
        <a-button class="btn-confirm" type="primary" @click="onSubmit">确定</a-button>
        <a-button @click="closeDialog()">取消</a-button>
      </div>
    </div>
  </div>
</template>
<script>

export default {
  name: "AccessPointDialog",
  data() {
    return {
      isPc: this.isPc(),
      errorMessage: '',
      isTrue: true,
      form: {
        password: '',
        new_password: '',
        new_password2: '',
      }
    }
  },
  props: {},
  watch: {},
  methods: {
    closeDialog() {
      this.$emit('closeDialog', false)
    },
    onSubmit() {
      let that = this;
      if (that.form.password === '' || that.form.new_password === '' || that.form.new_password2 === '') {
        that.errorMessage = "密码不能为空！";
        return false;
      }
      if (that.form.new_password.length < 6) {
        that.errorMessage = "新密码长度不能小于6位数！";
        return false;
      }
      if (that.form.new_password !== that.form.new_password2) {
        that.errorMessage = "两次密码输入不一致，请从新输入！";
        return false;
      }
      this.request.resetPassword(that.form).then(res => {
        if (res.code === 0) {
          that.errorMessage = res.msg;
          return false;
        } else {
          that.$message.success(res.msg);
          setTimeout(() => {
            that.$emit('closeDialog', false)
          }, 1000);
        }
      });
    },
    resendEmail() {

    },
  }
}
</script>

