<template>
    <div class="manage-account-container"  :class="{'header-below': isPc}">
        <div v-if="isLoading" :class="{'pc-loading': isPc, 'mobile-loading': !isPc}">
            <a-spin>
                <a-icon slot="indicator" type="loading" :style="{'font-size': 80 + 'px'}" spin/>
            </a-spin>
        </div>
        <div class="manage-account-box" v-if="isPc && !isLoading"
             :style="{'filter': showResetService ? 'blur(5px)': 'unset'}">
            <span class="title">管理账户</span>
            <div class="card-row-item">
                <div class="card-item">
                    <div class="card-icon au-address"></div>
                    <div class="card-content">
                        <span class="title">账户</span>
                        <span class="tips">管理账户相关的设定</span>
                        <div class="detail">
                            <span class="link">{{ userInfo.email }}</span>
                        </div>
                        <div class="operations">
                            <span class="operation reset-account" @click="resetPassword">重置密码</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <ResetPasswordDialog
            :class="{'dialog': isPc, 'dialog-out': !showResetService}"
            @closeDialog="closeDialog"
            v-show="showResetService"></ResetPasswordDialog>
        <div class="manage-account-box-mobile"
             v-if="!isPc && !isLoading">
            <span class="box-title">管理账户</span>
            <div class="row-box">
                <i class="i-account"></i>
                <span class="row-first">账户</span>
                <span class="row-sec">管理账户相关的设定 </span>
                <a-divider style="background-color: #E5E9ED;margin: 0.427rem 0;"/>
                <span class="row-third au-address">{{ userInfo.email }}</span>
                <div class="operations">
                    <span class="operation reset-account" @click="resetPassword">重置密码</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {AU_EMAIL, AU_EMAIL_STATUS, AU_HMAC, AU_SELECTED_SERVICE, AU_UUID} from "@/store/Global";
import ResetPasswordDialog from "../dialog/ResetPasswordDialog"

export default {
    name: "UserServicePanel",
    components: {
        ResetPasswordDialog,
    },
    data() {
        return {
            isLoading: true,
            showResetService: false,
            selectedService: localStorage.getItem(AU_SELECTED_SERVICE) ? JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)) : {},
            lang: this.$i18n.locale,
            auAddress: `${VUE_SITE}/user/service`,
            clientInfo: `{"uuid": ${localStorage.getItem(AU_UUID)}, "hmac": ${localStorage.getItem(AU_HMAC)}, "email":""}`,
            email: localStorage.getItem(AU_EMAIL) ? localStorage.getItem(AU_EMAIL).endsWith('@UnPanel.net') && localStorage.getItem(AU_EMAIL).length > 40 ? '' : localStorage.getItem(AU_EMAIL) : '',
            emailStatus: parseInt(localStorage.getItem(AU_EMAIL_STATUS)),
            intervalId: '',
            userInfo: {},
            isPc: this.isPc(),
            hideCopyTips: true,
        }
    },
    created() {
        this.getUserinfo()
    },
    mounted() {},
    destroyed() {
        window.clearInterval(this.intervalId);
    },
    methods: {
        getUserinfo() {
            let that = this;
            that.request.getUserinfo({}).then(res => {
                that.isLoading = false;
                that.userInfo = res.data;
            });
        },
        resetPassword() {
            if (!this.isPc) {
                window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
            }
            this.showResetService = true;
            this.$emit('dialogShow', true);
        },
        closeDialog(result) {
            if (result) {
                this.auAddress = `${VUE_SITE}?uuid=${localStorage.getItem(AU_UUID)}&hmac=${localStorage.getItem(AU_HMAC)}`;
            }
            if (this.showSwitchService) {
                this.selectedService = localStorage.getItem(AU_SELECTED_SERVICE) ? JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)) : {};
            }
            this.showResetService = false;
            this.$emit('dialogShow', false);
        },
    }
}
</script>

<style lang="scss">
/*#app {*/

/*}*/
/*.filter-select {*/
/*    /deep/ .au-panel-home {*/
/*        filter: blur(5px)*/
/*    }*/
/*    ::v-deep .au-panel-home {*/
/*        filter: blur(5px)*/
/*    }*/
/*    >>> .au-panel-home {*/
/*        filter: blur(5px)*/
/*    }*/
/*    filter: blur(5px)*/
/*}*/
.dialog {
    .dialog-box {
        box-shadow: 0 0 25px black;
        animation: zoomin 0.3s;
        animation-fill-mode: forwards;
    }

    /*.dialog-bg {*/
    /*  animation: bg-in 0.3s;*/
    /*  animation-fill-mode: forwards;*/
    /*}*/
    @keyframes bg-in {
        from {
            background: rgba(31, 34, 46, 0);
        }
        to {
            background: rgba(31, 34, 46, 0.7);
        }
    }
    @keyframes zoomin {
        from {
            opacity: 0;
            transform: scale(1.1) translate(-45%, -45%);
        }
        to {
            opacity: 1;
            transform: scale(1) translate(-50%, -50%);
        }
    }
}


.dialog-out {
    .dialog-box {
        animation: zoomout 0.3s;
        animation-fill-mode: forwards;
    }

    @keyframes zoomout {
        from {
            opacity: 1;
            transform: scale(1) translate(-50%, -50%);
        }
        to {
            opacity: 0;
            transform: scale(1.1) translate(-45%, -45%);
        }
    }
}

.manage-account-container {
    width: 100%;
    background: #F6F8FA;
    min-height: 100%;
    height: fit-content;
    overflow: auto;
    text-align: justify;

    .pc-loading {
        display: flex;
        justify-content: center;
        align-content: center;
        margin-top: 100px;
    }

    .mobile-loading {
        display: flex;
        justify-content: center;
        align-content: center;
        margin-top: 2rem /* 150/75 */
    ;
    }

    .manage-account-box {
        //display: flex;
        //flex-direction: column;
        width: 1080px;
        min-height: calc(100vh - 0.44rem);
        margin: 20px auto;
        box-shadow: 0 2px 6px rgba(234, 238, 246, 0.6);
        border-radius: 8px;
        background-color: #fff;
        position: relative;
        padding: 38px 40px;
        overflow: auto;
        text-align: justify;

        div {
            display: flex;
            flex-direction: row;
        }

        .title {
            font-size: 24px;
            font-weight: 500;
            color: #383E44;
            margin-bottom: 4px;
        }

        .card-row-item {
            flex-direction: row;
            justify-content: flex-start;
            margin-top: 24px;

            .card-item {
                min-width: 488px;
                max-width: 488px;
                height: 222px;
                padding: 30px 32px 20px 32px;
                border: 1px solid #E5E9ED;
                border-radius: 8px;
                margin-right: 24px;
                overflow: hidden;

                .card-icon {
                    width: 18px;
                    height: 18px;
                    margin-right: 16px;
                    background-size: contain;
                    content: '';
                }

                .au-address {
                    background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-account.svg') center no-repeat;
                }

                .email-bind {
                    background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-email.svg') center no-repeat;
                }

                .switch-service {
                    background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-switch-service.svg') center no-repeat;
                }

                .card-content {
                    flex-direction: column;
                    width: 100%;

                    .title {
                        font-size: 16px;
                        font-weight: 500;
                        color: #383E44;
                        line-height: 22px;
                        margin-bottom: 0;
                    }

                    .tips {
                        font-size: 14px;
                        font-weight: 400;
                        color: #A5B1BF;
                        line-height: 22px;
                        margin-top: 8px;
                    }

                    .detail {
                        flex-direction: row;
                        width: 100%;
                        height: 60px;
                        min-height: 60px;
                        border-top: 1px solid #E5E9ED;
                        border-bottom: 1px solid #E5E9ED;
                        margin-top: 20px;
                        justify-content: space-between;
                        align-items: center;

                        .download-btn {
                            font-size: 14px;
                            font-weight: 400;
                            line-height: 24px;
                            color: #3963EF;
                            display: inline-flex;
                            cursor: pointer;

                            &::before {
                                width: 20px;
                                height: 20px;
                                margin-right: 8px;
                                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-download.svg') center no-repeat;
                                background-size: contain;
                                content: '';
                            }
                        }

                        .link {
                            height: 100%;
                            padding: 18px 0;
                            width: 292px;
                            word-break: keep-all;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis
                        }

                        .copy-tips {
                            &:hover {
                                opacity: 1;
                            }

                            cursor: pointer;
                            position: absolute;
                            width: 292px;
                            height: 60px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            z-index: 1;
                            background-color: rgba(255, 255, 255, 0.7);
                            color: #3963EF;
                            font-size: 16px;
                            opacity: 0;
                        }

                        .center-email {
                            font-size: 14px;
                            font-weight: 400;
                            line-height: 22px;
                            color: #242C45;
                        }

                        .email-unbind {
                            font-size: 14px;
                            font-weight: 400;
                            line-height: 24px;
                            color: #242C45;
                            display: inline-flex;

                            &::before {
                                width: 20px;
                                height: 20px;
                                margin-right: 8px;
                                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-au-email-unbind.svg') center no-repeat;
                                background-size: contain;
                                content: '';
                            }
                        }

                        .email-bind {
                            &::before {
                                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-au-email-bind.svg') center no-repeat;
                            }
                        }
                    }

                    .operations {
                        height: 60px;
                        min-height: 60px;
                        align-items: center;

                        .operation {
                            font-size: 14px;
                            font-weight: 400;
                            line-height: 24px;
                            color: #3963ef;
                            cursor: pointer;
                            margin-right: 32px;
                        }

                    }
                }
            }
        }
    }

    .manage-account-box-mobile {
        //display: flex;
        flex-direction: column;
        width: 100%;
        min-height: 100%;
        box-shadow: 0 2px 6px rgba(234, 238, 246, 0.6);
        border-radius: 8px;
        background-color: #fff;
        position: relative;
        padding: 0.533rem;
        overflow: auto;
        text-align: justify;

        .box-title {
            font-size: 0.587rem;
            font-weight: 500;
            color: #383E44;
            line-height: 0.827rem;
            height: 0.827rem;
        }

        .row-box {
            display: flex;
            flex-direction: column;
            //align-items: center;
            border: 1px solid #D9DAE0;
            background: white;
            border-radius: 0.107rem;
            text-align: left;
            padding: 0.533rem;
            margin-top: 0.533rem;

            .i-account {
                width: 0.48rem;
                height: 0.48rem;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-account.svg') center no-repeat;
                background-size: contain;
            }

            .i-email {
                width: 0.48rem;
                height: 0.48rem;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-email.svg') center no-repeat;
                background-size: contain;
            }

            .i-switch-service {
                width: 0.48rem;
                height: 0.48rem;
                background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-switch-service.svg') center no-repeat;
                background-size: contain;
            }

            .row-first {
                font-size: 0.427rem;
                color: #383E44;
                margin: 0.32rem 0 0.213rem 0;
                font-weight: 500;
            }

            .row-sec {
                font-size: 0.373rem;
                color: #A5B1BF;
            }

            .au-address {
                word-break: keep-all;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }

            .download-span {
                margin-top: 0.373rem /* 28/75 */
            ;
                display: flex;
                color: #3963EF;
                font-size: 0.373rem;

                &::before {
                    width: 0.533rem;
                    height: 0.533rem;
                    margin-right: 0.107rem;
                    background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-download.svg') center no-repeat;
                    background-size: contain;
                    content: '';
                }
            }

            .row-third {
                font-size: 0.373rem;
                color: #242C45;
                overflow: hidden;
                word-wrap: break-word;
                word-break: break-all;
            }

            .row-four {
                font-size: 0.373rem;
                color: #3963EF;
            }

            .email-unbind {
                display: flex;
                align-items: center;
                font-size: 0.373rem;
                color: #242C45;

                &::before {
                    width: 0.373rem;
                    height: 0.373rem;
                    margin-right: 0.1rem;
                    margin-bottom: 0.1rem;
                    background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-au-email-unbind.svg') center no-repeat;
                    background-size: contain;
                    content: '';
                }
            }

            .email-bind {
                &::before {
                    background: url('https://cdn.jsdelivr.net/gh/UnPanel/unpanel-assets@v1.0.1/home/ic-au-email-bind.svg') center no-repeat;
                }
            }

            .operations {
                display: flex;
                flex-direction: row;

                .operation {
                    font-size: 0.373rem;
                    color: #3963EF;
                }

                .reset-account {
                    margin-right: 0.853rem /* 64/75 */
                ;
                }
            }
        }
    }
}

</style>
